import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowLeft,
  faCalendarAlt,
  faEye,
  faEyeSlash,
} from "@fortawesome/free-solid-svg-icons";
import "leaflet-control-geocoder/dist/Control.Geocoder.js";
import {
  Col,
  Row,
  Card,
  Form,
  Button,
  InputGroup,
  ButtonGroup,
  Dropdown,
  FormSelect,
} from "@themesberg/react-bootstrap";
import { useHistory } from "react-router-dom";
import MuiAlert from "@material-ui/lab/Alert";

import axios from "axios";
import Swal from "sweetalert2";


import { setOptions } from "leaflet";
import { RemoveFromQueue } from "@mui/icons-material";
import USstates from './UsStates.json'    
const Addadmin = () => {
  /*
  const center = {
    lat: 45.424721,
    lng: -75.695,
  };
  const icon = L.icon({
    iconSize: [25, 41],
    iconAnchor: [10, 41],
    popupAnchor: [2, -40],
    iconUrl: "https://unpkg.com/leaflet@1.6/dist/images/marker-icon.png",
    shadowUrl: "https://unpkg.com/leaflet@1.6/dist/images/marker-shadow.png",
  });
  const [position, setPosition] = useState(center);
  */
  const [passwordType, setPasswordType] = useState("password");
  const [place, setPlace] = useState("");
  const [country, setCountry] = useState("Canada");
  const [region, setRegion] = useState([
    {
      name: "Alberta",
      abbreviation: "AB",
    },
    {
      name: "British Columbia",
      abbreviation: "BC",
    },
    {
      name: "Manitoba",
      abbreviation: "MB",
    },
    {
      name: "New Brunswick",
      abbreviation: "NB",
    },
    {
      name: "Newfoundland and Labrador",
      abbreviation: "NL",
    },
    {
      name: "Northwest Territories",
      abbreviation: "NT",
    },
    {
      name: "Nova Scotia",
      abbreviation: "NS",
    },
    {
      name: "Nunavut",
      abbreviation: "NU",
    },
    {
      name: "Ontario",
      abbreviation: "ON",
    },
    {
      name: "Prince Edward Island",
      abbreviation: "PE",
    },
    {
      name: "Quebec",
      abbreviation: "QC",
    },
    {
      name: "Saskatchewan",
      abbreviation: "SK",
    },
    {
      name: "Yukon Territory",
      abbreviation: "YT",
    },
  ]);

  const history = useHistory();
  const togglePassword = () => {
    if (passwordType === "password") {
      setPasswordType("text");
      return;
    }
    setPasswordType("password");
  };
  /*
  function DraggableMarker() {
    const [draggable, setDraggable] = useState(true);
    const markerRef = useRef(null);

    const eventHandlers = useMemo(
      () => ({
        dragend() {
          const marker = markerRef.current;
          setPosition(marker.getLatLng());
          fetch(
            `https://eu1.locationiq.com/v1/reverse.php?key=pk.657f1ad662c5b8bc9407219fe04ddc5b&lat=${
              marker.getLatLng().lat
            }&lon=${marker.getLatLng().lng}&format=json`
          )
            .then((response) => response.json())
            .then((responseJson) => {
              setPlace(responseJson.display_name);
            });
        },
      }),
      []
    );

    return (
      <Marker
        draggable={draggable}
        eventHandlers={eventHandlers}
        icon={icon}
        position={position}
        ref={markerRef}
      >
        <Popup minWidth={90}>
          <span>{place}</span>
        </Popup>
      </Marker>
    );
  }
*/
  const [formData, setFormData] = useState({
    name: "",
    lastn: "",
    Datebirth: "",
    email: "",
    sex: "",
    password: "",
    confirmPassword: "",
    userimage: "",
    phone: "",
    role: "admin",
    gname: "",
    address: "",
    logitude: "",
    latitude: "",
    funeral_home: "",
  });
  const [birthday, setBirthday] = useState("");
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };
  async function Submit() {
    const mydata = new FormData();
    mydata.append("name", formData.name);
    mydata.append("lastn", formData.lastn);
    mydata.append("Datebirth", formData.Datebirth);
    mydata.append("email", formData.email);
    mydata.append("sex", formData.sex);
    mydata.append("password", formData.password);
    mydata.append("userimage", formData.userimage);
    mydata.append("phone", formData.phone);
    mydata.append("role", formData.role);
    mydata.append("gname", formData.gname);
    mydata.append("funeral_home", formData.funeral_home);
    mydata.append("address", formData.address );
    /*     mydata.append("latitude", position.lat);
    mydata.append("logitude", position.lng); */
    await axios
      .post("http://www.skiesbook.com:3000/api/v1/users/addadmin", mydata, config)
      .then((response) => {
        Swal.fire({
          position: "center",
          icon: "success",
          title: "Client ajouté avec succès",
          showConfirmButton: true,
        }).then((result) => {
          if (result.isConfirmed) {
            history.push("/AdminClients");
          }
        });
      })
      .catch((e) => {
        Swal.fire({
          position: "center",
          icon: "error",
          title: "Addresse mail existe déja",
          showConfirmButton: false,
          timer: 1500,
        });
      });
  }
  function handleChange(val) {
    console.log(val);
    setCountry(val);
    if (val === "Canada") {
      setRegion([
        {
          name: "Alberta",
          abbreviation: "AB",
        },
        {
          name: "British Columbia",
          abbreviation: "BC",
        },
        {
          name: "Manitoba",
          abbreviation: "MB",
        },
        {
          name: "New Brunswick",
          abbreviation: "NB",
        },
        {
          name: "Newfoundland and Labrador",
          abbreviation: "NL",
        },
        {
          name: "Northwest Territories",
          abbreviation: "NT",
        },
        {
          name: "Nova Scotia",
          abbreviation: "NS",
        },
        {
          name: "Nunavut",
          abbreviation: "NU",
        },
        {
          name: "Ontario",
          abbreviation: "ON",
        },
        {
          name: "Prince Edward Island",
          abbreviation: "PE",
        },
        {
          name: "Quebec",
          abbreviation: "QC",
        },
        {
          name: "Saskatchewan",
          abbreviation: "SK",
        },
        {
          name: "Yukon Territory",
          abbreviation: "YT",
        },
      ]);
    } else if (val === "Etats Unis") {
      setRegion(USstates);
    }
    setFormData({ ...formData, address: val  })
  }
  return (
    <Card border="light" className="bg-white shadow-sm mb-4">
      <Card.Body>
        <div className="btn-toolbar mb-2 mb-md-2">
          <ButtonGroup>
            <Dropdown.Toggle
              onClick={(e) => history.goBack()}
              as={Button}
              variant="primary"
              size="sm"
              className="me-2"
            >
              <FontAwesomeIcon icon={faArrowLeft} className="me-2" />
              Go back
            </Dropdown.Toggle>
          </ButtonGroup>
        </div>
        <h5 className="mb-4">General information</h5>
        <Form>
          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="firstName">
                <Form.Label>First Name</Form.Label>
                <Form.Control
                  required
                  type="text"
                  placeholder="Enter your first name"
                  onChange={(e) =>
                    setFormData({ ...formData, name: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
            <Col md={6} className="mb-3">
              <Form.Group id="lastName">
                <Form.Label>Last Name</Form.Label>
                <Form.Control
                  required
                  type="text"
                  placeholder="Also your last name"
                  onChange={(e) =>
                    setFormData({ ...formData, lastn: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
          </Row>
          <Row className="align-items-center">
            <Col md={6} className="mb-3">
              <Form.Group id="birthday">
                <Form.Label>Date de naissance</Form.Label>
                <Col md={6} className="mb-3">
                  <Form.Group id="birthday">
                    <InputGroup>
                      <Form.Control
                        className="date"
                        name="Datedeath"
                        type="date"
                        onChange={(e) =>
                          setFormData({
                            ...formData,
                            Datebirth: e.target.value,
                          })
                        }
                      />
                    </InputGroup>
                  </Form.Group>
                </Col>
              </Form.Group>
            </Col>

            <Col md={6} className="mb-3">
              <Form.Group id="gender">
                <Form.Label>Gender</Form.Label>
                <Form.Select
                  defaultValue="M"
                  onChange={(e) =>
                    setFormData({ ...formData, sex: e.target.value })
                  }
                >
                  <option value="0">Other</option>
                  <option value="F">Female</option>
                  <option value="M">Male</option>
                </Form.Select>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="emal">
                <Form.Label>Email</Form.Label>
                <Form.Control
                  required
                  type="email"
                  placeholder="name@company.com"
                  onChange={(e) =>
                    setFormData({ ...formData, email: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
            <Col md={6} className="mb-3">
              <Form.Group id="phone">
                <Form.Label>Phone</Form.Label>
                <Form.Control
                  required
                  type="number"
                  placeholder="(604) 555-5555"
                  onChange={(e) =>
                    setFormData({ ...formData, phone: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col md={6} className="mb-3">
              <Form.Group id="firstName">
                <Form.Label>Password</Form.Label>
                <InputGroup>
                  <Button
                    size="sm"
                    style={{
                      borderColor: "transparent",
                      marginRight: 5,
                      backgroundColor: "#d7dce4",
                    }}
                    onClick={() => togglePassword()}
                  >
                    {passwordType === "password" ? (
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEye} />
                      </InputGroup.Text>
                    ) : (
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEyeSlash} />
                      </InputGroup.Text>
                    )}
                  </Button>
                  <Form.Control
                    type={passwordType}
                    required
                    placeholder="Entrer votre mot de passe"
                    onChange={(e) =>
                      setFormData({ ...formData, password: e.target.value })
                    }
                  ></Form.Control>
                </InputGroup>
              </Form.Group>
            </Col>
            <Col md={6} className="mb-3">
              <Form.Group id="firstName">
                <Form.Label> Confirmer votre mot de passe</Form.Label>
                <InputGroup>
                  <Button
                    size="sm"
                    style={{
                      borderColor: "transparent",
                      marginRight: 5,
                      backgroundColor: "#d7dce4",
                    }}
                    onClick={() => togglePassword()}
                  >
                    {passwordType === "password" ? (
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEye} />
                      </InputGroup.Text>
                    ) : (
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEyeSlash} />
                      </InputGroup.Text>
                    )}
                  </Button>
                  <Form.Control
                    type={passwordType}
                    required
                    placeholder="Confirmer votre mot de passe"
                    onChange={(e) =>
                      setFormData({
                        ...formData,
                        confirmPassword: e.target.value,
                      })
                    }
                  ></Form.Control>
                </InputGroup>
                {formData.confirmPassword !== formData.password &&
                formData.confirmPassword !== "" ? (
                  <MuiAlert severity="warning">Password doesn't match</MuiAlert>
                ) : (
                  <span></span>
                )}
              </Form.Group>
            </Col>

            <Col md={6} className="mb-3">
              <Form.Group id="firstName">
                <Form.Label>Image</Form.Label>
                <Form.Control
                  required
                  type="file"
                  placeholder="Enter your password"
                  onChange={(e) =>
                    setFormData({ ...formData, userimage: e.target.files[0] })
                  }
                />
              </Form.Group>
            </Col>
          </Row>

          <h5 className="my-4">Informations du Cimetière </h5>
          <Row>
            <Col sm={6} className="mb-3">
              <Form.Group id="address">
                <Form.Label>Nom du Cimetière</Form.Label>
                <Form.Control
                  required
                  type="text"
                  placeholder="Enter Graveyard's name"
                  onChange={(e) =>
                    setFormData({ ...formData, gname: e.target.value })
                  }
                />
              </Form.Group>
            </Col>
            <Col sm={6} className="mb-3">
              <Form.Group id="address">
                <Form.Label>Country </Form.Label>
                <Form.Select
                  aria-label="Default select example"
                  onChange={(val) => handleChange(val.target.value)}
                  defaultValue="C"
                >
                  <option val="C">Canada</option>
                  <option val="US">Etats Unis</option>
                </Form.Select>
              </Form.Group>
            </Col>
            {/*    <Col sm={4} className="mb-3">
              <Form.Group id="address">
                <Form.Label>Maison Funéraire</Form.Label>
                <Form.Control
                  required
                  type="text"
                  placeholder="Donner votre Maison Funéraire"
                  onChange={(e) =>
                    setFormData({ ...formData, funeral_home: e.target.value })
                  }
                />
              </Form.Group>
            </Col> */}
          </Row>
          <Row>
            <Col md={6} className="mb-3">
              <Form.Label>Région</Form.Label>
              <Form.Group>
                <Form.Select
                  onBlur={(region) =>
                    setFormData({
                      ...formData,
                      address: formData.address.concat(
                        " ," + region.target.value
                      ),
                    })
                  }
                >
                  {region.map((option) => (
                    <option key={option}>{option.name}</option>
                  ))}
                </Form.Select>
              </Form.Group>
            </Col>
            <Col sm={3} className="mb-3">
              <Form.Group id="address">
                <Form.Label>Ville</Form.Label>
                <Form.Control
                  onBlur={(ville) =>
                    setFormData({
                      ...formData,
                      address: formData.address.concat(
                        " ," + ville.target.value
                      ),
                    })
                  }
                  required
                  type="text"
                  placeholder="Entrer la ville"
                />
              </Form.Group>
            </Col>
            <Col sm={2} className="mb-3">
              <Form.Group id="address">
                <Form.Label>Zip Code</Form.Label>
                <Form.Control
                  onBlur={(zipCode) =>
                    setFormData({
                      ...formData,
                      address: formData.address.concat(
                        " ," + zipCode.target.value
                      ),
                    })
                  }
                  required
                  type="text"
                  placeholder="Entrer votre zip code"
                />
              </Form.Group>
            </Col>
          </Row>
          {/*     <Card className="bg-white shadow-sm mb-4">
            <Row>
              <MapContainer center={center} zoom={13} scrollWheelZoom={true}>
                <TileLayer
                  attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                  url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                <DraggableMarker />
              </MapContainer>
            </Row>
          </Card> */}
          <div className="mt-3">
            <Button variant="primary" onClick={(e) => Submit()}>
              Save All
            </Button>
          </div>
        </Form>
      </Card.Body>
    </Card>
  );
};
export default Addadmin;
